'use client'

import Navbar from '@/components/Navbar';
import { useState, useEffect } from 'react';

export default function HomePage() {
  const [movies, setMovies] = useState([]);

  useEffect(() => {
    // Fetch movies from your API or state management
    const fetchMovies = async () => {
      const res = await fetch('/api/movies');
      const data = await res.json();
      setMovies(data);
    };

    fetchMovies();
  }, []);

  return (
    <div>
      <Navbar />

      {/* Dashboard */}
      <div className="px-4 py-6 bg-gray-900">
        <h2 className="text-3xl text-white mb-6">Películas y Series</h2>

        {/* Filtro de plataformas */}
        <div className="flex space-x-4 mb-6">
          <button className="bg-gray-800 p-2 text-white rounded hover:bg-gray-700">Netflix</button>
          <button className="bg-gray-800 p-2 text-white rounded hover:bg-gray-700">Amazon Prime</button>
          <button className="bg-gray-800 p-2 text-white rounded hover:bg-gray-700">Max</button>
          <button className="bg-gray-800 p-2 text-white rounded hover:bg-gray-700">Disney+</button>
        </div>

        {/* Dashboard de películas */}
        <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-6">
          {movies.map((movie) => (
            <div key={movie.id} className="bg-gray-800 p-4 rounded-lg">
              <img
                src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                alt={movie.title}
                className="w-full h-72 object-cover rounded mb-4"
              />
              <h3 className="text-white text-xl">{movie.title}</h3>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
